.search-container{
    display: flex;
    justify-content: center;
    margin:10px;
}
.search-input{
    padding: 10px;
    width:250px;
    font-size:15px;
    border-radius: 50px;
    background-color: #a592f2;
    border-width: 0;
    color:#e7e9f0;
    box-shadow: 0 0 5px rgba(255,255,255,0.5);
}
.search-input::placeholder{
    color:#e7e9f0;
}
.search-input:focus{
    outline:none;
}